:root {
    --main-color: #03204c;
    --secondary-color: rgb(176 204 224);
}

.loader {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    height: 100vh;
}

.loader img {
    width: 200px;
    height: auto;
    margin-bottom: 20px;
}

/* top close bar section */
.header-top {

    background-color: var(--secondary-color);

}

.header-top span {

    color: rgba(0, 0, 0, 0.555);
    padding: 0;
    font-weight: 600 !important;
    font-size: 25px;
    cursor: pointer;

}

.header-top span:hover {
    color: black;
}

.header-top button {
    background-color: var(--main-color);
    color: white;
    font-size: 14px;
    text-align: left !important;
    margin-left: 10px;
}

.header-top .navbar-brand {
    padding: 0 !important;
}

.header-top .smol {
    text-align: left;
}

.header-top p {
    color: var(--main-color);
}

/* navbar css starts here */
.sticky {
    position: sticky;
    top: 0;
    z-index: 999;
}

.top-f-nav {

    background-color: white !important;
    font-weight: 600;
    padding: 0;
}

.scrollspy .isCurrent .nav-link {
    background-color: var(--secondary-color) !important;
}

.scrollspy {

    display: flex;
    padding-left: 0;
    margin-bottom: 0;
    list-style: none;

}

html {
    scroll-behavior: smooth;
}

.scrollspy button {
    background-color: var(--main-color);
    border-color: var(--main-color);
    color: white;
    font-size: 13px;

}

.scrollspy button:hover {
    background-color: var(--secondary-color);
    border-color: var(--secondary-color);
    color: var(--main-color);


}

.scrollspy button:focus {
    background-color: var(--secondary-color);
    border-color: var(--secondary-color);
    color: var(--main-color);

}

/* navbar phone number */
.number a {
    color: var(--main-color) !important;
    font-weight: 600;
    text-decoration: none;
    font-size: 13px;
}

.scrollspy .nav-link {
    color: var(--main-color) !important;
    font-size: 13px;
    text-transform: uppercase;
}

.scrollspy .nav-item {
    padding-right: 18px;

}

.scrollspy .nav-link:hover {
    background-color: var(--secondary-color) !important;

}


